import Lines from './Lines.vue'
import CommodityList from './CommodityList.vue'
// 请求数据接口
import $http from '@/common/api/request.js'
export default {
	props:{
		keyDown:String
	},
	components:{
		Lines,
		CommodityList
	},
	data(){
		return {
			shoplist:{
				currentIndex:0,
				data:[
					{name:'价格',status:1,key:"pprice"},
					{name:'折扣',status:0,key:"discount"},
					{name:'品牌',status:0}
				]
			},
			shopListData:[]
		}
	},
	mounted() {
		// 数据源
		this.getShopData()
	},
	computed:{
		orderBy(){
			let obj = this.shoplist.data[this.shoplist.currentIndex]
			let val = obj.status === "1" ? "desc" : "asc"
			return {
				[obj.key] : val
			}
		}
	},
	methods:{
		// 点击导航栏进行跳转
		changeIndex(index){
			// 点击时刷新数据，点击排序 ===》 重新获取数据
			this.getShopData()
			// 获取他的索引值
			let idx = this.shoplist.currentIndex
			// 获取的具体是哪一个
			let item = this.shoplist.data[idx]
			if( idx == index ){
				return item.status = item.status === 1 ? 2 : 1; 
			}
			let newItem = this.shoplist.data[index]
			item.status = 0
			this.shoplist.currentIndex = index
			newItem.status = 1
			
		},
		getShopData(){
			$http.request({
				url:'/goods/search',
				data:{
					name:this.keyDown,
					...this.orderBy
				}
			}).then((res)=>{
				this.shopListData = res
			}).catch(()=>{
				uni.showToast({
					title:"请求失败",
					icon:"none"
				})
			})
		}
	}
	
}
